import React from 'react';
import { render } from 'react-dom';

import { MDXProvider } from '@mdx-js/react';

import {
  Deck,
  FlexBox,
  Slide,
  Box,
  Progress,
  FullScreen,
  Notes,
  mdxComponentMap,
  CodeSpan,
  Heading,
  Text,
  ListItem,
  UnorderedList
} from 'spectacle';

// Custom components


// SPECTACLE_CLI_MDX_START
import slides, { notes } from './slides.mdx';
// SPECTACLE_CLI_MDX_END


// SPECTACLE_CLI_THEME_START
const theme = {
  colors: {
    primary: '#484848',
    secondary: '#0000d0'
  },
  space: [8, 12, 18]
};
// SPECTACLE_CLI_THEME_END

const MyHeading = ({ fontSize, ...rest }) => (
  <Heading
    {...rest}
    fontFamily="Input Mono"
    fontSize={fontSize}
    fontWeight="300"
  />
);

const myComponents = {
  ...mdxComponentMap,
  inlineCode: props => <CodeSpan color="blue" {...props} />,
  h1: props => <MyHeading {...props} fontSize="15em" textAlign="left" letterSpacing="-30px" />,
  h2: props => <MyHeading {...props} fontSize="h2" />,
  h3: props => <MyHeading {...props} fontSize="h3" />,
  h4: props => <MyHeading {...props} fontSize="h4" />,
  p: props => (
    <Text {...props}
      fontFamily="Public Sans"
      width="max-content"
      lineHeight="1.6em"
    />
  ),
  li: props => (
    <ListItem
      {...props}
      lineHeight="1.6em"
      fontFamily="Public Sans"
    />
  ),
  ul: props => (
    <UnorderedList
      {...props}
    />
  )
};

const template = () => (
  <FlexBox
    position="absolute"
    bottom={0}
    width={1}
    justifyContent="center"
  >
  </FlexBox>
);

const Presentation = () => (
  <MDXProvider components={myComponents}>
    <Deck loop theme={theme} template={template}>
      {slides
        .map((MDXSlide, i) => [MDXSlide, notes[i]])
        .map(([MDXSlide, MDXNote], i) => (
          <Slide
            key={`slide-${i}`}
            slideNum={i}
            backgroundColor="#ffeded"
          >
            <FlexBox
              marginTop="auto"
              marginBottom="auto"
              paddingLeft="3em"
              paddingRight="3em"
              flexDirection="column"
              alignItems="center"
            >
              <MDXSlide />
            </FlexBox>
            <Notes>
              <MDXNote />
            </Notes>
          </Slide>
        ))}
    </Deck>
  </MDXProvider>
);

render(<Presentation />, document.getElementById('root'));
